import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { Button } from '../PrimaryStyles.js';
import { setUserTheme, setDarkModeTheme, setLightModeTheme, selectThemeName, darkModeTheme, lightModeTheme, userTheme } from '../../store/themeSlice.js';

const ThemeButton = ( {themeName, onSetUserTheme, onSetDarkTheme, onSetLightTheme, children} ) => {

    const changeTheme = () => {
        if (themeName === darkModeTheme.name) {
            onSetLightTheme();
        } else if (themeName === lightModeTheme.name) {
            onSetUserTheme();
        } else if (themeName === userTheme.name) {
            onSetDarkTheme();
        }
    }

    const Circle = styled.div`
        position: absolute;
        right: 50;
        top: 50;
    `
    return (
        <>
            <Button onClick = {changeTheme}>
                {children}
            </Button>
        </>
    );
}

{/* <svg onClick = {changeTheme}>
<circle r="20" fill='#FF0000'/>
</svg> */}

const mapStateToProps = state => ({
    themeName: selectThemeName(state)
});
const mapDispatchToProps = dispatch => ({
    onSetUserTheme: () => dispatch(setUserTheme()),
    onSetDarkTheme: () => dispatch(setDarkModeTheme()),
    onSetLightTheme: () => dispatch(setLightModeTheme())
});

export default connect(mapStateToProps, mapDispatchToProps)(ThemeButton);