import React, { useState } from "react";
import styled, { keyframes } from 'styled-components';

import StickerImg1 from "../../../res/Sticker_App_Cen_01.png";
import { Header } from "../../PrimaryStyles";

const Content = [
    {
        src: StickerImg1,
        title: "Bulk Image Manipulation Windows Application"
    },
    {
        src: StickerImg1,
        title: "WAH"
    }
]

const Image = styled.img`
    width: 100%;
    grid-column: d / f;
`

const ProjectH1 = styled(Header)`
    grid-column: b / d;
`

const ProjectDiv = styled.div`
    grid-column: b / f;
    z-index: -1;

    transform: translate(${props => props.pos}%);
    transition: transform 0.5s ease;

    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 100%);
    column-gap: 20%;
`

const ProjectInstanceDiv = styled.div`
    width: 100%;
    display: flex;
    flex-flow: column;
    flex-grow: 1;
`



const ProjectInstance = ({item}) => {
    return (
        <ProjectInstanceDiv>
            <ProjectH1>{item.title}</ProjectH1>
            <Image src={item.src} ></Image>
        </ProjectInstanceDiv>
    );
}

const Project = ({pos}) => {


    return (

        <ProjectDiv pos={pos}>
            {Content.map(item => <ProjectInstance item={item}/>)}
        </ProjectDiv>

    );
}

export default Project;