import React, { useState } from 'react';
import styled from 'styled-components';

import Project from './Project.js';

import { Header , BEWrapper } from '../../PrimaryStyles';

import { connect } from 'react-redux';

import { selectThemeBackgroundColor, selectThemeMainAccentColor, selectThemeFontColor } from '../../../store/themeSlice';

const Triangle = ({ w = '30', h = '30', direction = 'right'}) => {
    const points = {
      top: [`${w / 2},0`, `0,${h}`, `${w},${h}`],
      right: [`0,0`, `0,${h}`, `${w},${h / 2}`],
      bottom: [`0,0`, `${w},0`, `${w / 2},${h}`],
      left: [`${w},0`, `${w},${h}`, `0,${h / 2}`],
    }
  
    return (
      <svg width={w} height={h}>
        <polygon points={points[direction].join(' ')} />
      </svg>
    )
}

const LeftArrow = styled.div`
    grid-column: a / b;
    grid-row: 2 / 3;
    justify-self: center;
    align-self: center;

    cursor: pointer;
    transition: fill ease 0.2s;
    fill: ${(props) => props.theme.mainAccentColor};
    :hover {
        fill: ${(props) => props.theme.fontColor};
    }
`
const RightArrow = styled.div`
    grid-column: f / g;
    grid-row: 2 / 3;
    justify-self: center;
    align-self: center;

    cursor: pointer;
    transition: fill ease 0.2s;
    fill: ${(props) => props.theme.mainAccentColor};
    :hover {
        fill: ${(props) => props.theme.fontColor};
    }
`

const ProjectWrapper = styled(BEWrapper)`
    grid-template-columns: [a] 6% [b] 22% [c] 22% [d] 22% [e] 22% [f] 6% [g];
    grid-column: b / e;
    grid-row: 1 / -1;
    @media (max-width: 1000px) {
        grid-column: a /e;
        grid-row: b / c;
    }
    
    overflow: auto;
    ::-webkit-scrollbar {
        width: 0px;
        height: 0px;
        display: none;
        background: transparent;
        -ms-overflow-style: none; 
    }
`

const ModHeader = styled(Header)`
    grid-column: a / g;
`

const ProjectContainer = ({themeBackGroundColor, themeMainAccentColor}) => {

    const [pos, setPos] = useState(0);

    const HandleClick = (event, dir) => {
        if (dir === "left") setPos(pos - 120);
        if (dir === "right") setPos(pos + 120);
    }

    return (
        <ProjectWrapper>
            <LeftArrow onClick={event => HandleClick(event, "right")}>
                <Triangle direction='left' color={themeMainAccentColor}/>
            </LeftArrow> 
            <ModHeader>Projects:</ModHeader>
            <RightArrow onClick={event => HandleClick(event, "left")}>
                <Triangle direction='right' color={themeMainAccentColor}/>
            </RightArrow>

            <Project pos={pos} setPos={setPos} />

        </ProjectWrapper>
    );
}


const mapStateToProps = state => ({
    themeBackGroundColor: selectThemeBackgroundColor(state),
    themeMainAccentColor: selectThemeMainAccentColor(state),
    themeFontColor: selectThemeFontColor(state)
});
const mapDispatchToProps = dispatch => ({});


export default connect(mapStateToProps, mapDispatchToProps)(ProjectContainer);